.container {
  width: 327px;
  margin: 0 auto;
  padding: 0 24px;
}
@media (min-width: 768px) {
  .container {
    width: 672px;
    padding: 0 48px;
  }
}
@media (min-width: 1024px) {
  .container {
    width: 928px;
  }
}
@media (min-width: 1280px) {
  .container {
    width: 1120px;
    padding: 0 80px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1244px;
    padding: 0 98px;
  }
}
@media (min-width: 1920px) {
  .container {
    width: 1660px;
    padding: 0 130px;
  }
}

body {
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 12px;
  line-height: 18px;
  color: #383636;
  min-width: 375px;
  height: 100vh;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@keyframes notificator {
  0% {
    transform: rotate(0deg);
    transition: 0.6s;
  }
  40% {
    transform: rotate(50deg);
    transition: 0.6s;
  }
  80% {
    transform: rotate(-50deg);
    transition: 0.6s;
  }
  100% {
    transform: rotate(0deg);
    transition: 0.6s;
  }
}
.main-header {
  background-color: #f8f8f7;
}

.main-header__page-head {
  display: none;
}

.main-nav {
  position: fixed;
  display: flex;
  background-color: #ffffff;
  left: 0;
  right: 0;
  height: 40px;
  padding: 12px 24px;
  z-index: 9;
}

.main-nav__logo {
  display: block;
  background-image: url("../img/mobile/logo.svg");
  background-repeat: no-repeat;
  width: 43px;
  height: 40px;
}

.main-nav__nav-list {
  font-family: "Inter", "Arial", sans-serif;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  background-color: #ffffff;
  height: 100vw;
  margin: 0;
  padding: 30px 0 0 0;
  list-style: none;
  transform: translateY(-150%);
  transition: 0.4s;
  z-index: 3;
}
.main-nav__nav-list--open {
  transform: translateY(64px);
  transition: 0.4s;
}

.main-nav__nav-item {
  height: 20vw;
}
.main-nav__nav-item a {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  text-transform: uppercase;
  text-decoration: none;
  color: #383636;
  width: 100%;
  height: 100%;
  transition: 0.4s;
}
.main-nav__nav-item a:hover, .main-nav__nav-item a:focus {
  background-color: #e78346;
  opacity: 0.8;
  transition: 0.4s;
}

.main-nav__profile-block {
  display: flex;
  width: 130px;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
}

.main-nav__notifications {
  width: 22px;
  height: 24px;
  background-image: url("../img/icons/notification.svg");
  background-repeat: no-repeat;
  background-color: transparent;
  padding: 0;
  border: none;
  cursor: pointer;
  transition: 0.1s;
}
.main-nav__notifications:hover {
  animation-name: notificator;
  animation-duration: 0.6s;
  transition: 0.6s;
}

.main-nav__profile-link {
  display: block;
  width: 40px;
  height: 40px;
  background-image: url("../img/mobile/profile-icon.png");
  background-repeat: no-repeat;
  cursor: pointer;
}

.main-nav__menu-button {
  width: 40px;
  height: 40px;
  background-image: url("../img/icons/menu-button.svg");
  background-repeat: no-repeat;
  background-color: transparent;
  padding: 0;
  border: none;
  cursor: pointer;
  transition: 1s;
}
.main-nav__menu-button:hover {
  transform: rotate(360deg);
  transition: 1s;
}

.main-content {
  display: flex;
  flex-direction: column;
  background-color: #f8f8f7;
}

.all-pages {
  display: none;
}

.titles {
  background-color: #ffffff;
  border-radius: 20px;
  margin-bottom: 43px;
  padding: 34px 24px;
  order: 1;
}

.titles__titles-info {
  position: relative;
}

.titles__title {
  font-size: 24px;
  line-height: 29px;
  margin-top: 0;
  margin-bottom: 4px;
}

.titles__my-points {
  font-family: "Inter", "Arial", sans-serif;
  font-size: 16px;
  line-height: 16px;
  font-weight: 600;
  color: #5b8388;
  margin: 0;
}

.titles__points-history {
  line-height: 14px;
  text-decoration: none;
  color: #adadab;
  display: inline-block;
  margin-top: 32px;
  margin-bottom: 22px;
  border-bottom: 1px solid #eeeeee;
  transition: 0.4s;
}
.titles__points-history:hover {
  opacity: 0.6;
  transition: 0.4s;
}

.titles__my-sale {
  font-family: "Inter", "Arial", sans-serif;
  font-size: 14px;
  line-height: 14px;
  font-weight: 600;
  color: #e78346;
  width: 112px;
  position: relative;
  margin: 0;
}
.titles__my-sale:hover, .titles__my-sale:focus {
  transition: 0.4s;
}
.titles__my-sale:hover + .titles__sale-info, .titles__my-sale:focus + .titles__sale-info {
  opacity: 0.9;
}
.titles__my-sale::after {
  content: "";
  position: absolute;
  top: -2px;
  right: -28px;
  background-image: url("../img/icons/quest.svg");
  background-repeat: no-repeat;
  width: 18px;
  height: 18px;
}

.titles__sale-info {
  line-height: 14px;
  color: #ffffff;
  width: 187px;
  height: 96px;
  position: absolute;
  top: -16px;
  left: 16px;
  background-color: rgba(56, 54, 54, 0.9);
  padding: 14px 18px 16px 18px;
  border-radius: 10px;
  z-index: 10;
  opacity: 0;
  transition: 0.4s;
}
.titles__sale-info span {
  font-family: "Inter", "Arial", sans-serif;
  color: #e78346;
}

.titles__sale-sum {
  color: #adadab;
  margin-top: 0;
  margin-bottom: 16px;
}

.titles__you-sale {
  font-weight: 600;
  margin: 0;
}
.titles__you-sale span {
  margin-left: 34px;
}

.titles__team-sale {
  font-weight: 600;
  margin-top: 16px;
  margin-bottom: 0;
}
.titles__team-sale span {
  margin-left: 14px;
}

.titles__titles-list {
  display: flex;
  column-gap: 8px;
  overflow-x: auto;
  margin: 0;
  padding: 40px 0 0 0;
  list-style: none;
}

.titles__titles-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.titles__titles-item--iron .titles__titles-image {
  margin-top: -12px;
  z-index: 5;
}
.titles__titles-item--iron .titles__titles-name {
  margin-top: 49px;
}

.titles__titles-name {
  line-height: 14px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  width: 87px;
  margin-top: 53px;
  margin-bottom: 7px;
}

.titles__titles-points {
  font-family: "Inter", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 600;
  margin: 0;
}

.titles__title-sale {
  font-family: "Inter", "Arial", sans-serif;
  font-size: 10px;
  line-height: 12px;
  font-weight: 600;
  color: #adadab;
}

.titles__level-bonus {
  line-height: 14px;
  font-weight: 600;
  color: #5b8388;
  position: relative;
  display: block;
  background-color: #f8f8f7;
  margin: 21px 0;
  padding: 12px 29px 12px 54px;
  border-radius: 20px;
}
.titles__level-bonus::before {
  content: "";
  position: absolute;
  left: 13px;
  width: 30px;
  height: 30px;
  background-image: url("../img/icons/bonus.svg");
  background-repeat: no-repeat;
}

.titles__rating-info {
  line-height: 14px;
  text-decoration: none;
  color: #adadab;
  display: inline-block;
  border-bottom: 1px solid #eeeeee;
  transition: 0.4s;
}
.titles__rating-info:hover {
  opacity: 0.6;
  transition: 0.4s;
}

.info-bonus {
  font-family: "Inter", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  text-decoration: none;
  position: relative;
  color: #a8a8a8;
  display: inline-block;
  background-color: #ffffff;
  padding: 11px 11px 11px 51px;
  border-radius: 20px;
}
.info-bonus::before {
  content: "";
  position: absolute;
  top: 18px;
  left: 11px;
  background-image: url("../img/icons/pen.svg");
  background-repeat: no-repeat;
  width: 24px;
  height: 24px;
}

.info-bonus__bonus-number {
  color: #383636;
}

.profile {
  margin-top: 64px;
}

.profile__intro-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 26px;
}

.profile__description-block {
  margin-right: 42px;
}

.profile__my-name {
  font-size: 14px;
  line-height: 21px;
  color: #b6b6b3;
  margin: 0;
}

.profile__my-status {
  font-size: 20px;
  line-height: 28px;
  font-weight: 600;
  margin: 12px 0;
}

.profile__my-info {
  line-height: 15px;
  margin: 0;
  color: #b6b6b3;
}

.profile__friends {
  margin-top: 27px;
  margin-bottom: 30px;
}

.profile__friends-title {
  font-size: 14px;
  line-height: 20px;
  color: #b6b6b3;
  margin: 0;
}

.profile__all-friends {
  display: block;
  background-image: url("../img/mobile/friend-list.png");
  background-repeat: no-repeat;
  width: 141px;
  height: 48px;
  margin-top: 8px;
}

.profile__links {
  display: flex;
  flex-wrap: wrap;
  margin-top: 14px;
  margin-bottom: 23px;
}

.profile__links-title {
  font-size: 14px;
  line-height: 20px;
  flex-basis: 100%;
  color: #b6b6b3;
  margin-top: 0;
  margin-bottom: 5px;
}

.profile__link {
  position: relative;
  display: flex;
  align-items: center;
  line-height: 15px;
  text-decoration: none;
  font-weight: 600;
  color: #383636;
  padding-left: 32px;
  transition: 0.4s;
}
.profile__link:hover, .profile__link:focus {
  opacity: 0.6;
  transition: 0.4s;
}
.profile__link::before {
  content: "";
  position: absolute;
  left: 0;
  background-repeat: no-repeat;
  width: 24px;
  height: 24px;
}
.profile__link--vkontakte {
  flex-basis: 45%;
}
.profile__link--vkontakte::before {
  background-image: url("../img/icons/vk.svg");
}
.profile__link--facebook {
  flex-basis: 35%;
}
.profile__link--facebook::before {
  background-image: url("../img/icons/fb.svg");
}
.profile__link--discord {
  margin-top: 15px;
}
.profile__link--discord::before {
  background-image: url("../img/icons/discord.svg");
}

.main-footer {
  background-color: #383636;
  padding: 35px 0;
}

.main-footer__footer-logo {
  display: block;
  width: 203px;
  height: 34px;
  background-image: url("../img/mobile/footer-logo.svg");
  background-repeat: no-repeat;
}

.main-footer__our-service {
  display: none;
}

.main-footer__our-info {
  display: none;
}

.main-footer__other-info {
  display: none;
}

.main-footer__contacts-block {
  font-family: "Inter", "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  margin-top: 35px;
}
.main-footer__contacts-block a {
  font-size: 16px;
  line-height: 16px;
  text-decoration: none;
  color: #ffffff;
}

.main-footer__number {
  margin-bottom: 8px;
}

.main-footer__social-block {
  display: flex;
  column-gap: 8px;
  width: 232px;
  margin-top: 32px;
}

.main-footer__social-link {
  display: inline-block;
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
  transition: 0.4s;
}
.main-footer__social-link:hover, .main-footer__social-link:focus {
  opacity: 0.6;
  transition: 0.4s;
}
.main-footer__social-link--whatsapp {
  background-image: url("../img/icons/footer-whts.svg");
}
.main-footer__social-link--vkontakte {
  background-image: url("../img/icons/footer-vk.svg");
}
.main-footer__social-link--facebook {
  background-image: url("../img/icons/footer-fb.svg");
}
.main-footer__social-link--youtube {
  background-image: url("../img/icons/footer-ytb.svg");
}
.main-footer__social-link--telegram {
  background-image: url("../img/icons/footer-tlg.svg");
}

.main-footer__copyright-block {
  font-family: "Inter", "Arial", sans-serif;
  color: rgba(255, 255, 255, 0.3);
  margin-top: 28px;
  padding-top: 21px;
  border-top: 1px solid rgba(233, 232, 229, 0.1);
}

.main-footer__copyright {
  margin: 0;
}

.main-footer__all-right {
  margin: 10px 0;
}

.main-footer__confidentiality {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.3);
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.main-footer__made-by {
  display: block;
  width: 113px;
  height: 18px;
  background-image: url("../img/icons/flat12.svg");
  background-repeat: no-repeat;
  margin-top: 35px;
}

.edit-profile {
  background-color: #ffffff;
  margin-top: 117px;
  padding: 31px 24px 40px 24px;
  border-radius: 20px;
}

.edit-profile__go-back {
  display: block;
  background-image: url("../img/icons/arrow-back.svg");
  background-repeat: no-repeat;
  width: 32px;
  height: 32px;
  transition: 0.4s;
}
.edit-profile__go-back:hover, .edit-profile__go-back:focus {
  transform: scale(1.25);
  transition: 0.4s;
}

.edit-profile__title {
  line-height: 29px;
  margin-top: 16px;
  margin-bottom: 40px;
}

.edit-photo {
  margin-top: 16px;
  margin-bottom: 34px;
}

.edit-photo__photo-block {
  display: flex;
  background-color: #ffffff;
  margin-bottom: 16px;
  padding: 24px 59px 24px 24px;
  border-radius: 20px;
}

.edit-photo__download-zone {
  width: 168px;
  margin-left: 32px;
}

.edit-photo__photo-info {
  font-size: 14px;
  line-height: 21px;
  color: #adadab;
  margin-top: 0;
  margin-bottom: 8px;
}

.edit-photo__download {
  text-transform: uppercase;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e78346;
  width: 112px;
  height: 37px;
  border-radius: 34px;
  cursor: pointer;
  transition: 0.4s;
}
.edit-photo__download:hover, .edit-photo__download:focus {
  opacity: 0.7;
  transition: 0.4s;
}

.form__title {
  margin-top: 0;
  margin-bottom: 32px;
}

.form__contact-block {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  border: none;
}

.form__contact-label {
  font-family: "Inter", "Arial", sans-serif;
  text-transform: uppercase;
  font-size: 10px;
  line-height: 12px;
  font-weight: 600;
  color: #adadab;
  display: flex;
  flex-direction: column;
}
.form__contact-label:nth-of-type(2) {
  margin-top: 32px;
  margin-bottom: 30px;
}
.form__contact-label input {
  font-size: 14px;
  line-height: 21px;
  color: #383636;
  margin-top: 9px;
  border: none;
  border-bottom: 1px solid #adadab;
}

.form__history-nicknames {
  font-size: 12px;
  line-height: 17px;
  font-weight: 600;
  text-transform: uppercase;
  text-decoration: none;
  color: #5b8388;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(91, 131, 136, 0.1);
  width: 135px;
  height: 17px;
  padding: 10px 20px 10px 49px;
  border-radius: 26px;
  transition: 0.4s;
}
.form__history-nicknames:hover, .form__history-nicknames:focus {
  opacity: 0.6;
  transition: 0.4s;
}
.form__history-nicknames::before {
  content: "";
  position: absolute;
  left: 21px;
  background-image: url("../img/icons/history-nicknames.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.form__personal-block {
  margin: 63px 0 0 0;
  padding: 0;
  border: none;
}

.form__personal-label {
  font-family: "Inter", "Arial", sans-serif;
  text-transform: uppercase;
  font-size: 10px;
  line-height: 12px;
  font-weight: 600;
  color: #adadab;
  display: flex;
  flex-direction: column;
}
.form__personal-label:not(:first-of-type) {
  margin-top: 32px;
}
.form__personal-label input {
  font-size: 14px;
  line-height: 21px;
  color: #383636;
  margin-top: 9px;
  border: none;
  border-bottom: 1px solid #adadab;
}

.form__city {
  font-size: 14px;
  line-height: 21px;
  color: #adadab;
  margin-top: 9px;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #adadab;
}
.form__city option {
  color: #383636;
}

.form__save-button {
  font-size: 16px;
  line-height: 22px;
  font-weight: 600;
  text-transform: uppercase;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e78346;
  width: 327px;
  height: 50px;
  margin-top: 41px;
  padding: 0;
  border: none;
  border-radius: 34px;
  cursor: pointer;
  transition: 0.4s;
}
.form__save-button:hover, .form__save-button:focus {
  opacity: 0.7;
  transition: 0.4s;
}